




















































import { Component, Vue } from 'vue-property-decorator'
// import UserLabel from './UserLabel.vue'
const userData: any = require('@/user.js').default

const createChatData = () => {
  return {
    label: '夜鹰',
    mark: '拔剑最快 & 职业电灯泡',
    uid: 'eagle',
    value: ''
  }
}

@Component({
  components: {
    // UserLabel
  }
})
export default class Main extends Vue {
  // @Prop() private msg!: string
  private data: any = {}
  private toastStatus: boolean = false
  private changeData: any = {}
  private switchModal: boolean = false
  private selectData: any = {}

  private mounted() {
    this.createScenes()
    this.selectData = Object.assign({}, userData)
  }
  private createTime() {
    return new Date().valueOf().toString()
  }

  private copy(value: any): void {
    let res = null
    if (typeof value === 'object') {
      res = 'export default ' + JSON.stringify(value)
    } else {
      res = value
    }
    const input: any = this.$refs.copy
    input.value = res
    input.select()
    document.execCommand('copy')
    this.toastStatus = true
    setTimeout(() => {
      this.toastStatus = false
    }, 600)
  }

  private addConversation(e: any, key: string, user: string, bool: boolean) {
    const res = this.data
    this.updateData(res)
    if (!bool) {
      return
    }
    if (e) {
      // 快捷键添加
      if ((e.metaKey && e.keyCode === 13) || (e.ctrlKey && e.keyCode === 13)) {
        // Cmd + Enter || Ctrl + Enter
      } else {
        return
      }
    }
    // 点击添加
    switch (user) {
      case 'res':
        res[key].res.push(createChatData())
        break
      case 'req':
        res[key].req.push({
          value: '',
          next: key
        })
      default:
        break
    }
    this.updateData(res)
    // 聚焦
    if (user === 'res') {
      // TODO
    } else {
      // TODO
    }
  }

  private deleteConversation(key: string, user: string, index: number) {
    const res = this.data
    switch (user) {
      case 'res':
        res[key].res.splice(index, 1)
        break
      case 'req':
        res[key].req.splice(index, 1)
        break
      default:
        break
    }
    this.updateData(res)
  }

  private createScenes(): void {
    const res: any = this.data
    const time = this.createTime()
    res[time] = {
      res: [],
      req: []
    }
    this.updateData(res)
    this.addConversation(null, time, 'res', true)
    this.addConversation(null, time, 'req', true)
  }

  private changeUser(e: any, ekey: string, eindex: number) {
    this.changeData = {
      x: e.clientX,
      y: e.clientY,
      key: ekey,
      index: eindex,
      data: this.data
    }
    this.switchModal = true
  }

  private hanldeSelect(e: any, vkey: string): void {
    e.stopPropagation()
    const res = this.data
    const { key, index } = this.changeData
    const select = this.selectData[vkey]
    const obj = res[key].res[index]
    Object.assign(obj, select)
    this.updateData(res)
    this.switchModal = false
  }

  private updateData(res: any): void {
    this.data = {}
    this.data = res
  }
}
